<template>
  <div class="add-scene">
    <el-form
      :model="sceneForm"
      ref="sceneForm"
      label-width="120px"
      class="demo-ruleForm"
    >
      <el-form-item
        label="场景值"
        prop="scene"
        :rules="[{ required: true, message: '请输入场景值', target: 'blur' }]"
      >
        <el-input
          v-model="sceneForm.scene"
          clearable
          placeholder="请输入场景值"
        />
        <div class="tip">
          温馨提示：<br />
          1）场景值是对“扫描小程序码”进入“来问十月菌”进行标记<br />
          2）场景值是对生成码的标记，建议使用场景名称的首字母缩写<br />
          若场景值中有年月日，可显示：YYYY.MM.DD
        </div>
      </el-form-item>
      <el-form-item
        label="备注"
        prop="remark"
        :rules="[{ required: true, message: '请输入备注信息', target: 'blur' }]"
      >
        <el-input
          v-model="sceneForm.remark"
          clearable
          placeholder="请输入备注信息"
        />
        <div class="tip">
          备注内容便于后续查找以及管理
        </div>
      </el-form-item>
      <el-form-item prop="setUp">
        <el-button type="primary" @click="submitForm('sceneForm')">
          {{ btnText }}
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { createProgramCode } from '@/api/assistantManage'
export default {
  data() {
    return {
      btnText: '确认新增',
      sceneForm: {
        scene: '',
        remark: ''
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.createProgramCode()
        } else {
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    async createProgramCode() {
      const params = {
        scene: this.sceneForm.scene,
        remark: this.sceneForm.remark
      }
      const { code, message } = await createProgramCode(params)
      if (code === 10000) {
        this.$message.success(message)
        this.$router.push({ name: 'programeCode' })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.add-scene {
  /deep/.el-input {
    width: 220px;
    input {
      height: 32px;
      line-height: 32px;
    }
  }
  .tip {
    margin-top: 10px;
    line-height: 16px;
  }
  .btn-style {
    border: 1px solid rgba(228, 230, 233, 1);
    border-radius: 4px;
    vertical-align: middle;
  }
}
</style>
